@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap');

/* Ensure full width of html and body */
html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  overflow-x: hidden; /* Prevent horizontal scrolling */
  box-sizing: border-box;
  background-color: #fffefe;
  }

.project-section {
  display: block;
  width: 100vw;
  padding: 0;
  margin: 0;
  text-align: center;
  overflow: hidden;
  background-color: transparent;
}

.project-content {
  z-index: 1; /* Ensure the content is above the background and overlay */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  padding-top: 10px;
  color: white; /* Change text color to white to stand out on dark background */
}

.project-content h1 {
  font-size: 4rem; /* Adjust font size as needed */
  color: rgb(255, 255, 255);
}

/* ! >> Variables and Mixins
==============  =========================================== */
.anima1 {
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.anima-none {
  -webkit-transition: none!important;
  -moz-transition: none!important;
  transition: none!important;
}
.anima-transform {
  -webkit-transition-property: -webkit-transform;
  -moz-transition-property: -moz-transform;
  -o-transition-property: -o-transform;
  transition-property: transform;
}
.background-cover,
.bg-cover {
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.elpt-portfolio {
  padding-top: 20px;
  padding-bottom: 20px;
}
.elpt-portfolio-filter {
  text-align: center;
  padding-bottom: 20px;
}
.elpt-portfolio-filter .portfolio-filter-item {
  border: 0;
  background: #333;
  color: #fff;
  padding: 8px 12px;
  border-radius: 5px;
  margin-right: 3px;
  margin-bottom: 3px;
  font-size: 13px;
  outline: none!important;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.elpt-portfolio-filter .portfolio-filter-item:hover {
  background: #b11818;
  opacity: 0.9;
}
.elpt-portfolio-filter .portfolio-filter-item.item-active {
  background: #555;
  opacity: 0.8;
  outline: none!important;
}
.elpt-portfolio-content:after {
  content: '';
  clear: both;
}

.elpt-portfolio-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; /* Centers the items horizontally */
  gap: 20px; /* Adds space between the items */
}

.elpt-portfolio-content .portfolio-item-wrapper {
  width: calc(33.33% - 20px); /* Adjust the width to account for the gap */
  margin-bottom: 20px; /* Space between rows */
}

@media (min-width: 768px) {
  .elpt-portfolio-content .portfolio-item-wrapper {
    width: calc(50% - 20px); /* Two items per row for medium screens */
  }
}

@media (min-width: 992px) {
  .elpt-portfolio-content .portfolio-item-wrapper {
    width: calc(33.33% - 20px); /* Three items per row for large screens */
  }
}


.elpt-portfolio-content .portfolio-item-wrapper img {
  width: 100%;
  -webkit-transition: all 0.4s ease-in-out;
  -moz-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  background: #f4f4f4;
}
.elpt-portfolio-content .portfolio-item {
  display: block;
  position: relative;
  width: 100%;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  background-position: center center;
  overflow: hidden;
}
.elpt-portfolio-content p {
  display: none;
}
